<template>
  <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px">
    <el-form-item label="邮箱账号" prop="account">
      <el-input type="email"  v-model="ruleForm.account" autocomplete="off"></el-input>
    </el-form-item>
    <el-form-item label="密码" prop="pass">
      <el-input type="password" v-model="ruleForm.pass" autocomplete="off"></el-input>
    </el-form-item>
    <el-form-item label="确认密码" prop="checkPass" v-show="state">
      <el-input type="password" v-model="ruleForm.checkPass" autocomplete="off"></el-input>
    </el-form-item>
    <el-form-item style="display: flex; margin-left: 20px">
      <el-button type="primary" @click="submitForm('ruleForm')">提交</el-button>
      <el-button @click="resetForm('ruleForm')">重置</el-button>
      <el-button circle icon="el-icon-refresh" @click="changeState"></el-button>
    </el-form-item>
  </el-form>
</template>

<script>
import axios from "axios";
import qs from "qs";

export default {
  name: 'RegisterForm',
  data() {
    let checkAccount = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入账号'));
      }else if (value.search(/[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/i) === -1) {
        callback(new Error('账号格式不正确'))
      }
      callback()

    };
    let passWordPass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'));
      } else {
        if (this.ruleForm.checkPass !== '') {
          this.$refs.ruleForm.validateField('checkPass');
        }
        callback();
      }
    };
    let checkPassWordPass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请再次输入密码'));
      } else if (value !== this.ruleForm.pass) {
        callback(new Error('两次输入密码不一致!'));
      } else {
        callback();
      }
    };
    return {
      ruleForm: {
        pass: '',
        checkPass: '',
        account: ''
      },
      rules: {
        pass: [
          { validator: passWordPass, trigger: 'blur' }
        ],
        checkPass: [
          { validator: checkPassWordPass, trigger: 'blur' }
        ],
        account: [
          { validator: checkAccount, trigger: 'blur' }
        ]
      },
      msg: ''
    };
  },
  props: ['state'],
  methods: {
    open(text, type) {
      if (type !== 'error') {
        this.$message({
          message: text,
          type: type
        })
      }else
        this.$message.error(text)
    },
    checkBox(title, message, showCancelButton, beforeClose) {
      this.$msgbox({
        title,
        message,
        showCancelButton,
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        beforeClose
      }).then(action => {
        this.open(...this.msg);
      });
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          const h = this.$createElement;
          let message = h('p', null, '是否提交？')
          let beforeClose = (action, instance, done) => {
            if (action === 'confirm') {
              instance.confirmButtonLoading = true;
              instance.confirmButtonText = '提交中...';
              let aim = this.state === true? 'register': 'login'
              let obj = {
                account: this.ruleForm.account,
                password: this.ruleForm.pass
              }
              console.log(obj)
              console.log(qs.stringify(obj, {indices: false}))
              // let url = `http://localhost:8080/api/user/${aim}`
              let url = `/user/${aim}`
              console.log(url)
              axios.post(url, qs.stringify(obj, {indices: false})).then(
                  (res) => {
                    let data = res.data
                    console.log(data)
                    this.msg = [data['info'], data['status']]
                    instance.confirmButtonLoading = false
                    done()
                  },
                  (err) => {
                    this.msg = ['连接失败', 'error']
                    done()
                  }
              )
            } else {
              done();
            }
          }
          this.checkBox('验证信息', message, true, beforeClose)
        } else {
          this.open('验证失败', 'error')
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    changeState() {
      this.$bus.$emit('switchState')
    }
  }
}
</script>