<template>
  <div>
    <el-container>
      <el-aside :width="barWidth">
        <NavBar style="overflow: hidden"/>
      </el-aside>
      <el-container>
        <el-header>
          <Header/>
        </el-header>
        <el-container>
          <el-main style="padding: 2px 1px 5px 5px; margin-top: 20px">
            <router-view></router-view>
          </el-main>
        </el-container>
      </el-container>
    </el-container>
  </div>
</template>

<style>

.el-aside {
  transition: width 600ms ease;
}

</style>

<script>
import Header from "@/layouts/default/Header";
import NavBar from "@/layouts/default/NavBar";
export default {
  name: 'Home',
  components: {
    NavBar,
    Header
  },
  data() {
    return {
      barWidth: '203px'
    }
  },
  methods: {
    changeSize(state) {
      this.barWidth = state? '65px': '201px'
    }
  },
  mounted() {
    this.$bus.$on('changeSize', this.changeSize)
  }
}
</script>
