<template>
  <div>
    <el-autocomplete
        v-model="keyWord"
        :placeholder="roadText"
        :fetch-suggestions="querySearch"
        @input="searchSpotInfo([keyWord, city])"
        @keydown.enter.native="searchSpots"
        @select="handleSelect"
    >
      <i class="el-input__icon el-icon-delete" slot="suffix" @click="deleteDestinations" v-if="flag < 3"></i>
      <i class="el-input__icon el-icon-search" slot="suffix" @click="searchSpots" v-else></i>
      <template slot-scope="{ item }">
        <div class="name">{{ item.name }}</div>
        <span class="address">{{ item.address }}</span>
      </template>
    </el-autocomplete>
    <el-tooltip content="搜索" placement="top" v-if="search">
      <el-button circle icon="el-icon-search" class="search" @click="searchSpots"></el-button>
    </el-tooltip>
  </div>
</template>

<script>
import {mapActions, mapState} from "vuex";

export default {
  name: "Search",
  data() {
    return {
      keyWord: '',
      handler: null,
    }
  },
  props: ['roadText', 'search', 'flag'],
  computed: {
    ...mapState('MapOptions', {spotList: 'spotList'}),
    city() {
      return localStorage.getItem('city')
    },

  },
  methods: {
    ...mapActions('MapOptions', {searchSpotInfo: 'searchSpotInfo'}),
    querySearch(queryString, cb) {
      let spotList = this.spotList
      clearTimeout(this.handler)
      this.handler = setTimeout(() => {
        let results = queryString ? spotList.filter(this.createFilter(queryString)) : spotList;
        cb(results);
      }, 1000)
      // 调用 callback 返回建议列表的数据
    },
    createFilter(queryString) {
      return (spot) => {
        return (spot.name.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
      };
    },
    searchSpots() {
      this.$bus.$emit('drawSpots', this.keyWord)
    },
    handleSelect(item) {
      this.keyWord = item.name
      if (!this.search) {
        this.$bus.$emit('getPath', {
          flag: this.flag,
          location: item.location,
          name: item.name
        })
      }
    },
    deleteDestinations() {
      if (this.keyWord.length > 0) {
        this.$bus.$emit('deleteItem', this.keyWord)
        this.keyWord = ''
      }
    }
  }
}
</script>

<style scoped>
.search {
  margin-left: 5px;
}
.name {
  text-overflow: ellipsis;
  overflow: hidden;
}
.address {
  font-size: 12px;
  color: #b4b4b4;
}
</style>