<template>
  <div>
    <transition name="el-zoom-in-top">
      <el-button icon="el-icon-s-unfold" circle @click="change" v-show="!state1" class="menu-button" style="margin-left: 0"></el-button>
    </transition>
    <transition name="el-zoom-in-bottom">
      <el-button icon="el-icon-s-fold" circle @click="change" v-show="state2" class="menu-button" style="margin-left: 0"></el-button>
    </transition>
  </div>
</template>

<script>
export default {
  name: "MenuSwitchButton",
  data() {
    return {
      state1: false,
      state: false,
      state2: false
    }
  },
  methods: {
    change() {
      if (!this.state) {
        this.state = !this.state
        this.state1 = !this.state1
        this.$bus.$emit('switch', this.state)
        setTimeout(() => {
          this.$bus.$emit('changeSize', this.state)
          this.state2 = !this.state2
        }, 100)
      }else {
        this.state = !this.state
        this.state2 = !this.state2
        this.$bus.$emit('changeSize', this.state)
        setTimeout(() => {
          this.state1 = !this.state1
          this.$bus.$emit('switch', this.state)
        }, 100)
      }
    }
  }
}
</script>

