<template>
  <el-dropdown size="mini">
    <slot></slot>
    <el-dropdown-menu slot="dropdown">
      <el-dropdown-item v-for="(item, index) in dropDownItems" :key="item">
        <div v-if="index === dropDownItems.length - 1" @click="quit">{{item}}</div>
        <div v-else>{{item}}</div>
        <hr v-if="index === dropDownItems.length - 2">
      </el-dropdown-item>
    </el-dropdown-menu>
  </el-dropdown>
</template>

<script>
import Cookies from "js-cookie";
export default {
  name: "DropDown",
  props: ['dropDownItems'],
  methods: {
    open(text, type) {
      if (type !== 'error') {
        this.$message({
          message: text,
          type: type
        })
      }else
        this.$message.error(text)
    },
    checkBox(title, message, showCancelButton, beforeClose) {
      this.$msgbox({
        title,
        message,
        showCancelButton,
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        beforeClose
      }).then(action => {
        Cookies.remove('id')
        this.open('退出成功', 'success')
        this.$router.replace('/login')
      })
    },
    quit() {
      const h = this.$createElement;
      let message = h('p', null, '是否退出？')
      let beforeClose = (action, instance, done) => {
        if (action === 'confirm') {
          instance.confirmButtonLoading = true;
          instance.confirmButtonText = '退出中...';
          done()
        } else {
          done();
        }
      }
      this.checkBox('验证信息', message, true, beforeClose)
    },
  }
}
</script>

