<template>
  <div>
    <el-card class="loginForm" shadow="hover">
      <div slot="header" class="clearfix">
        <span>登录</span>
      </div>
      <transition name="el-zoom-in-bottom" mode="in-out">
        <RegisterForm style="position:relative;right: 25px" :state="state" v-show="state" />
      </transition>
      <el-collapse-transition>
        <LoginForm style="position:relative;right: 25px" :state="state1&state" v-show="state1"/>
      </el-collapse-transition>

    </el-card>
    <particles-bg type="random" :canvas="{backgroundColor:'#888'}" :bg="true"/>
  </div>
</template>

<script>
import LoginForm from "@/components/LoginForm";
import RegisterForm from "@/components/RegisterForm";
import { ParticlesBg } from "particles-bg-vue";
import Cookies from "js-cookie";
export default {
  name: "Login",
  components: {
    LoginForm,
    ParticlesBg,
    RegisterForm
  },
  data() {
    return {
      state: true,
      state1: false
    }
  },
  methods: {
    checkLogin() {
      let id = Cookies.get('id')
      if (id) {
        this.$router.replace({name: 'file', params:{auth: 'true'}})
      }
    }
  },
  mounted() {
    this.$bus.$on('switchState', () => {
      if (this.state) {
        this.state = !this.state
        setTimeout(() => {
          this.state1 = !this.state1
        },200)
      }else {
        this.state1 = !this.state1
        setTimeout(() => {
          this.state = !this.state
        }, 300)
      }
    })
    this.checkLogin()
  }
}
</script>

<style scoped>
.loginForm {
  position: relative;
  width: 400px;
  top: 200px;
  left: 550px;
  text-align: center;
  background-color: ghostwhite;
}

</style>