import Vue from 'vue'
import VueRouter from 'vue-router'
import File from "@/views/File";
import PersonalFile from "@/views/PersonalFile";
import Profession from "@/views/Profession";
import Home from "@/views/Home";
import Login from "@/views/Login";
Vue.use(VueRouter)

const routes = [
  {
    name: 'home',
    path: '/home',
    component: Home,
    children: [
      {
        name: 'file',
        path: 'file',
        component: File
      },
      {
        name: 'personalFile',
        path: 'personalFile',
        component: PersonalFile
      },
      {
        name: 'profession',
        path: 'profession',
        component: Profession
      }
    ]
  },
  {
    name: 'login',
    path: '/login',
    component: Login
  },
  {
    name: 'index',
    path: '/',
    redirect: '/login'
  },

]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router
