<template>
  <div style="margin-top: 20px;width: 1101px; align-self: center">
    <el-table
        :data="fileList"
        border
        >
      <el-table-column
          fixed
          prop="file_name"
          label="文件名"
          width="450">
      </el-table-column>
      <el-table-column
          prop="account"
          label="账号名"
          width="300">
      </el-table-column>
      <el-table-column
          label="操作"
          width="350">
        <template slot-scope="scope">
          <el-button type="text" size="small" @click="openPreView(scope.row)">查看</el-button>
          <el-button type="text" size="small" @click="handleClick(scope.row)" >下载</el-button>
          <el-button type="text" size="small" @click="removeItem(scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog
        title="预览"
        :visible.sync="dialogVisible"
        width="50%"
        >
      <iframe :src="href" style="width: 700px; height: 600px; text-align: center"></iframe>
      <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
  </span>
    </el-dialog>
  </div>
</template>

<script>
import axios from "axios";
import qs from "qs";
import Cookies from "js-cookie";

export default {
  name: "ShowFiles",
  data() {
    return {
      fileList: [],
      dialogVisible: false,
      href: '',
      msg: ''
    }
  },
  methods: {
    open(text, type) {
      if (type !== 'error') {
        this.$message({
          message: text,
          type: type
        })
      }else
        this.$message.error(text)
    },
    checkBox(title, message, showCancelButton, beforeClose) {
      this.$msgbox({
        title,
        message,
        showCancelButton,
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        beforeClose
      }).then(action => {
        this.$message.success('删除成功')
        this.getData()
      })
    },
    handleClick(row) {
      //http://localhost:8080/api/file/dowload_files?url=./static${row['file']}&name=${row['file_name']}
      window.location.href = `/file/dowload_files?url=./static${row['file']}&name=${row['file_name']}`
    },
    getData() {
      // http://localhost:8080/api/file/show_files
      axios.post('/file/show_files', qs.stringify({account: Cookies.get('id')}, {indices: false}))
          .then(
              res => {
                this.fileList = res.data['file_list']
              }
          )
    },
    openPreView(row) {
      this.dialogVisible = true
      //http://localhost:8080/api/static${row['file']}
      this.href = `/static${row['file']}`
      console.log(this.href)
    },
    removeItem(row) {
      const h = this.$createElement;
      let message = h('p', null, '是否提交？')
      let beforeClose = (action, instance, done) => {
        if (action === 'confirm') {
          instance.confirmButtonLoading = true;
          instance.confirmButtonText = '提交中...';
          //http://localhost:8080/api/file/delete_file?id=${row['id']}
          axios.get(`/file/delete_file?id=${row['id']}`).then(
              (res) => {
                let data = res.data
                console.log(data)
                this.msg = data['status']
                instance.confirmButtonLoading = false
                done()
              },
              (err) => {
                this.msg = ['连接失败', 'error']
                done()
              }
          )
        } else {
          done();
        }
      }
      this.checkBox('验证信息', message, true, beforeClose)
    }
  },
  mounted() {
    this.getData()
    this.$bus.$on('refreshComponent', this.getData)
  }
}
</script>

