import axios from "axios";

const state = {
  city: '',
  spotList: [],
  point: '',
  temp: '',
}

const actions = {
  getIpAddress(context) {
    axios.get('http://localhost:8080/BMap/location/ip?ak=TdFnlwfseG2vzNxISSeAaaoMLU27h9sI&coor=bd09ll').then(
      response => {
        context.commit('getIpAddress', {
          city: response.data.content.address_detail.city,
          point: {
            x: response.data.content.point.x,
            y: response.data.content.point.y
          }
        })
      },
      error => {
        console.log(error)
      }
    )
  },
  searchSpotInfo(context, value) {
    let url = `http://localhost:8080/BMap/place/v2/suggestion?query=${value[0]}&region=${value[1]}&output=json&ak=TdFnlwfseG2vzNxISSeAaaoMLU27h9sI&coor=bd09ll`
    console.log(value[1])
    if (typeof value[0] === "undefined") {
      value[0] = ''
    }
    if (Math.abs( value[0].length - state.temp.length) >= 2) {
      state.temp = value[0]
      axios.get(url)
        .then(
          response => {
            context.commit('searchSpotInfo', response.data.result)
          },
          error => {

          }
        )
    }

  }
}

const mutations = {
  getIpAddress(state, value) {
    state.city = value.city
    state.point = value.point
  },
  searchSpotInfo(state, value) {
    state.spotList = value
}
}



export default {
  namespaced: true,
  state,
  actions,
  mutations
}