<template>
  <div class="files">
    <UploadFiles/>
    <ShowFiles/>
  </div>
</template>

<script>
import Cookies from "js-cookie";
import UploadFiles from "@/components/UploadFiles";
import ShowFiles from "@/components/ShowFiles";
export default {
  name: "File",
  data() {
    return {
      account: Cookies.get('id')
    }
  },
  components: {
    UploadFiles,
    ShowFiles
  },
  beforeRouteEnter(to, from ,next) {
    if (to.params.auth === 'true' || Cookies.get('id'))
      next()
    else
      this.$router.replace({name: 'login'})
  }
}
</script>

<style scoped>
.files {
  display: flex;
  display: -webkit-flex;
  flex-direction:column;
}
</style>